@import "./default.scss";

.header{
    width: 100%;
    padding: 18px 0;
    background-color: $orange;
    display: flex;
    justify-content: center;

    .logo-container{
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-size: 2.4em;

        img{
            display: block;
            margin-right: 7px;
        }
    }
}

.trips-container{
    max-width: 550px;
    margin: 50px auto;
}

.trip{
    display: flex;
    align-items: center;
    padding-bottom: 18px;
    margin-bottom: 15px;
    border-bottom: 1px solid #F5F5F5;
}

.trip:last-child{
    border: 0;
}

.trip-img-container{
    min-width: 125px;
    height: 168px;
    background-size: cover;
    background-position: center;
    border-radius: 5px;
}

.trip-info-container{
    margin-left: 20px;
    font-size: 1.2em;

    .location{
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .loc-title{
            display: inline-block;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            margin-right: 12px;
        }

        img{
            display: inline-block;
            width: 7px;
            height: auto;
            margin-right: 4px;
        }

        .gmap{
            color: #918E9B;
            border-bottom: 1px solid #918E9B;
        }
    }

    .title{
        font-weight: bold;
        font-size: 2.4em;
        margin-top: 4px;
        margin-bottom: 14px;
    }

    p{
        margin-top: 0.9em;
        margin-bottom: 0;
    }
}

.footer{
    background-color: $black;
    color: #fff;
    padding: 18px 0;
    text-align: center;

    h1{
        font-weight: bolder;
        letter-spacing: 0.2em;
        margin: 0;
    }
}